.headerSection {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100vw;
    padding-right: 10rem;
    background-color: lightgrey;
}

.controls {
    display: flex;
    align-items: center;
    margin: .5rem;
}

.controls img {
    width: 30px;
    height: 30px;
}

.logo {
    margin-left: 1rem;
    width: unset;
    height: 30px;
}

.bodySection {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.up {
    transition: all .25s ease-out !important;
}

.down {
    transition: all .25s ease-out !important;
    transform: rotate(-90deg);
}

.open {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.closed {
    display: none;
}